@import 'src/common/style/variables';

@font-face {
  font-family: PhillySans;
  src: url("./assets/fonts/PhillySans.otf");
}

body {
  #root {
    height: 100vh;
    background-color: $bg-color;

    .app-container,
    .page-container {
      height: 100%;
    }
  }

  &.has-opened {
    $animation-duration: 1s;
    $animation-delay: 500ms;

    #root {
      .logo-short {
        transition: fill $animation-duration $animation-delay;
        fill: $dark-color;
      }

      .title-name,
      .title-surname {
        transition: $animation-duration $animation-delay;
        opacity: 1;
      }

      .title-name {
        right: 42%;
      }

      .title-surname {
        left: 38%;
      }
    }
  }
}

* {
  //font-family: 'PhillySans', sans-serif;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  letter-spacing: 0;
  line-height: 1;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: $light-color;
}

.btn {
  padding: 10px 20px;
  color: $dark-color;
  width: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  border: 1px solid $dark-color;
  cursor: pointer;

  &.confirm-btn {
    color: $dark-color;
    @include success-gradient-bg;
  }

  &.delete-btn {
    color: $white-color;
    @include danger-gradient-bg;
  }
}
