.main-page {
  position: relative;
  height: 100%;

  .main-page-logo {
    height: 60%;
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .title {
    position: absolute;
    z-index: 2;
    font-size: 10rem;
    letter-spacing: -0.5rem;
    top: 50%;
    opacity: 0;

    $gap: 5rem;

    &.name {
      right: 32%;
      transform: translateY(calc(-100% - $gap / 2));
    }

    &.surname {
      left: 28%;
      transform: translateY($gap / 2);
    }
  }
}
